 false {\n    $context-setter: private-breakpoint-set-context('no-query', $query-fallback);\n  }\n\n  // Print Out Query String\n  @if not breakpoint-get('no queries') {\n    @media #{$query-string} {\n      @content;\n    }\n  }\n\n  @if breakpoint-get('no query fallbacks') != false or breakpoint-get('no queries') == true {\n\n    $type: type-of(breakpoint-get('no query fallbacks'));\n    $print: false;\n\n    @if ($type == 'bool') {\n      $print: true;\n    }\n    @else if ($type == 'string') {\n      @if $query-fallback == breakpoint-get('no query fallbacks') {\n        $print: true;\n      }\n    }\n    @else if ($type == 'list') {\n      @each $wrapper in breakpoint-get('no query fallbacks') {\n        @if $query-fallback == $wrapper {\n          $print: true;\n        }\n      }\n    }\n\n    // Write Fallback\n    @if ($query-fallback != false) and ($print == true) {\n      $type-fallback: type-of($query-fallback);\n\n      @if ($type-fallback != 'bool') {\n        #{$query-fallback} & {\n          @content;\n        }\n      }\n      @else {\n        @content;\n      }\n    }\n  }\n\n  @include private-breakpoint-reset-contexts();\n}\n\n\n@mixin mq($query, $no-query: false) {\n  @include breakpoint($query, $no-query) {\n    @content;\n  }\n}\n","\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n@import \"~@/styles/theme/breaks\";\n@import \"~@/styles/theme/colors\";\n.WinePage {\n  $B: #{&};\n  &--header {\n    @include Break( (max-width Breaks(4)) (min-width Breaks(2)) ){\n      flex-flow: wrap row !important;\n      > #{$B}--stats {\n        flex: 1 65%;\n        width: 65%;\n      }\n      > #{$B}--media {\n        flex: 1 35%;\n        width: 35%;\n      }\n      >:last-child {\n        min-width: 100%;\n      }\n    }\n    @include Break( min-width Breaks(3) ){\n      // min-height: 90vh;\n    }\n  }\n  &--detail {\n    &-tables {\n      display: flex;\n      margin: 1.5rem 0;\n      min-width: 50vw;\n      \n      border-color: Color(theme) !important;\n      border-style: solid !important;\n      border-width: 0/*1px*/ 0;\n      border-width: 0;\n      \n      table {\n        &, th, td { border-color: inherit !important }\n        border-bottom: 0 solid;\n        margin: 0 .75rem 0;\n        tr:only-child { td, th {\n          border-bottom: 1px solid;\n        } }\n        th {\n          vertical-align: middle;\n          line-height: 1.2;\n          + td { text-align: left }\n        }\n      }\n    }\n    @include Break( min-width Breaks(3) ){\n      &-tables table th {\n        text-align: right;\n        white-space: nowrap;\n      }\n    }\n    @include Break( max-width Breaks(3) ){\n      &-tables {\n        width: 100%;\n        flex-flow: nowrap column;\n        table {\n          &:not(:last-child) { margin-bottom: 0 }\n          + table {\n            margin-top: 0;\n            border-top: 1px solid;\n          }\n          th { width: 33% }\n        }\n      }\n    }\n  }\n  // &--stats {}\n  &--media {\n    flex: 0 auto !important;\n    padding-left: 1rem !important;\n    padding-right: 1rem !important;\n    @include Break( max-width Breaks(2) ){\n      order: -1;\n      margin-bottom: -2rem;\n      // margin-top: .5rem;\n      // margin-bottom: -9rem;\n      // padding-top:  !important;\n      background: Color(cream);\n    }\n    img {\n      mix-blend-mode: multiply;\n      max-height: 42em;\n      max-width: 100%;\n      width: auto;\n      height: auto;\n\n      height: auto;\n      // min-height: 75vh;\n      max-height: c